body {
  width: 100%;
  background-color: grey;
  color: white;
}

a {
  color: white;
}

a:hover {
  color: red;
}

.heading-container {
  margin: 0 auto;
  margin-top: 20px;
  width: 790px;
  text-align: center;
}

.heading-container-title {
  font-size: 20px;
  font-weight: bold;
}

.menu-container {
  margin: 0 auto;
  width: 790px;
  text-align: center;
  border-bottom: 1px solid #000000;
}

.menu-item {
  color: white;
  display: inline-block;
  list-style: none;
  margin-left: 25px;
  margin-right: 25px;
  border: 1px solid #000000;
  border-radius: 10px;
  padding: 15px;
}

.body-container {
  width: 790px;
  height: auto;
  margin: 0 auto;
  overflow: hidden;
  display: block;
}

.content-container {
  width: 790px;
  height: auto;
  margin: 20px 0px 20px 0px;
}

.footer-container {
  padding-top: 20px;
  margin: 0px auto 20px auto;
  width: 790px;
  text-align: center;
  border-top: 1px solid #000000;
}

.hide {
  display: none;
}